body,
button,
input,
select,
textarea {
    color: $color__text-main;
    //font for all content
    font-family: $font__aa;
    @include font-size(.9);
    line-height: $font__line-height-body;
    @media screen and (min-width :700px){
        @include font-size(1);
    }
}

@import "headings";

@import "copy";