		flex: 2;
	}
	.f-widget .widget:nth-last-child(n+4),
	.f-widget .widget:nth-last-child(n+4) ~ .widget, page-widget .widget:nth-last-child(n+4),
	page-widget .widget:nth-last-child(n+4) ~ .widget {
		width: 30%;
		flex: 1 1 30%;
	}
}

