	padding-bottom: .5em;
}

.widget_pages a,
.widget_nav_menu a {
	display: block;
	padding: 1em 0;
	text-decoration: none;
	border-bottom: 1px dashed gray;
}

.widget_pages a:hover, .widget_pages a:focus,
.widget_nav_menu a:hover,
.widget_nav_menu a:focus {
	text-decoration: none;
	border-bottom: 1px dashed gray;
}

.widget_pages li,
.widget_nav_menu li {
	padding: 0;
}

/* Meta widget */
.widget_meta li {
	padding: 0;
}

/* Recent comments */
.widget_recent_comments li {
	line-height: 1.3em;
	padding-bottom: .5em;
}

.widget_recent_comments a:hover,
.widget_recent_comments a:focus {
	padding-left: 0;
}

/* RSS widget */
.widget_rss li {
	margin-bottom: 1em;
}

.widget li a.rsswidget {
	display: block;
	padding-right: .5em;
	font-size: 1em;
	line-height: 1.4em;
}

.rss-date,
.widget_rss cite {
	color: #8DCDBC;
	font-size: .7em;
	font-family: "Cormorant Garamond", Helvetica, serif;
}

.rssSummary {
	padding: .5em 0;
	font-family: "Cormorant Garamond", Helvetica, serif;
	font-size: .8em;
	line-height: 1.4em;
}

/* Search widget */
.widget_search input[type="search"] {
	box-sizing: border-box;
	width: 100%;
	padding: .5em 1em;
	border: 1px solid #c3c3c3;
	border-radius: 0;
}

/* Calendar widget */
.widget_calendar {
	font-weight: 400;
}

.widget_calendar caption {
	padding: .5em 0;
	font-size: 1em;
	font-weight: 500;
	text-align: left;
}

.widget_calendar thead {
	background: transparent;
	color: black;
}

.widget_calendar thead th {
	border-bottom-width: 2px;
}

.widget_calendar td {
	padding: .2em;
	font-size: .8em;
	text-align: center;
	background: white;
	border: 1px solid #cccccc;
}

.widget_calendar th {
	border: none;
	text-align: center;
}

.widget_calendar .pad {
	background: #eaeaea;
}

.widget_calendar a {
	font-weight: 900;
	text-decoration: none;
}

/* Text widget */
.textwidget {
	font-size: 80%;
	line-height: 1.3em;
}

/* Footer widget stylings */
#footer-widget-area {
	width: 100%;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}

@media screen and (max-width: 600px) {
	#footer-widget-area {
		flex-direction: column;
	}
}

.widget {
	min-width: 30%;
	/* width:auto;*/
	/*flex: 1 1 30%;*/
}

aside#secondary {
	margin-top: 40px;
}

.page-widget {
	max-width: 50%;
	flex: 0 0 33%;
}

.f-widget, page-widget {
	margin: 2em 0 0;
	padding: 1em 0;
	background-color: #FF9990;
	color: #99ccff;
}

@media screen and (min-width: 600px) {
	.f-widget, page-widget {
		padding: 2em 1em;
	}
}

.f-widget section.widget, page-widget section.widget {
	flex: 1;
	margin: 0 1em 2em;
	padding-top: 1em;
}

.f-widget section.widget .original-title, page-widget section.widget .original-title {
	color: #FF9990;
}

.f-widget .widget,
.f-widget .widget-title,
.f-widget .widget a,
.f-widget .widget_calendar thead,
.f-widget .rss-date,
.f-widget .widget_rss cite, page-widget .widget,
page-widget .widget-title,
page-widget .widget a,
page-widget .widget_calendar thead,
page-widget .rss-date,
page-widget .widget_rss cite {
	color: white;
}

.f-widget .widget_calendar td,
.f-widget .widget_calendar a, page-widget .widget_calendar td,
page-widget .widget_calendar a {
	color: black;
}

@media screen and (min-width: 900px) {
	.f-widget, page-widget {
		/*		 Use flex to create a responsive layout */
		display: flex;
		flex-wrap: wrap;
		justify-content: space-around;
		/*		 If only one widget is displayed, make it full width. */
		/*		 If 2 or 3 widgets are displayed, make the second one twice as wide. */
		/*		 If 4 or more widgets are displayed, show three across when available. */
	}
	.f-widget .widget:only-of-type, page-widget .widget:only-of-type {
		width: 100%;
	}
	.f-widget .widget:nth-last-child(n+2):nth-last-child(-n+3):first-child ~ .widget:nth-child(2), page-widget .widget:nth-last-child(n+2):nth-last-child(-n+3):first-child ~ .widget:nth-child(2) {
		flex: 2;
	}
	.f-widget .widget:nth-last-child(n+4),
	.f-widget .widget:nth-last-child(n+4) ~ .widget, page-widget .widget:nth-last-child(n+4),
	page-widget .widget:nth-last-child(n+4) ~ .widget {
		width: 30%;
		flex: 1 1 30%;
	}
}

