.sticky {
    display: block;
}

.hentry {
    margin: 0 0 1.5em;
    max-width: 950px;
}
.entry-header{
    font-family:$font__aa;
}

.cat-links{
    font-size:90%;
    font-weight:700;
    a{
        font-decoration:none;
        text-transform: uppercase;
        color: $color__interactive;
        &:hover, &:focus{
            background-color: $color__interactive;
            color:$color__text-main;
            padding:2px;
            @include border-radius(5px);
        }
    }
}

.entry-title{
    margin: .125em 0 .25em;
    font-size:2.5em;
    @media screen and (min-width:$query__med){
        font-size:3em;
    }

    a{
        text-decoration: none;
        color:$color__skin;
    }
    :focus, :hover{
        border-bottom:5px solid $color__interactive;
    }
}
.entry-meta{
    font-size:90%;
    a{
        color:$color__interactive;
        font-weight:300;
        text-decoration: none;
        &:focus, &:hover{
            color:$color__skin;
            border-bottom: 2px double $color__interactive;
        }
    }
}
.byline{
    &::after {
        content: "||";
        margin: 0 .5em;
    }
}

.comments-link,
.edit-link{
    &::before {
        content: "||";
        margin: 0 .5em;
    }
}

.byline,
.updated:not(.published){
    display: none;
}

.single .byline,
.group-blog .byline {
    display: inline;
}

.page-content,
.entry-content,
.entry-summary {
    margin: 1.5em 0 0;
    max-width:100%;
}

.page-links {
    clear: both;
    margin: 0 0 1.5em;
}
.page-container{
    display:flex;
       @media screen and (max-width: $query__small) {
		flex-direction:column-reverse;
                
	}
}
@media screen and (min-width: $query__small){
    .page-main{
        min-width:100%;
    }
}
.page-main {
     min-width:66%;
    flex: 0 1 66%;
}

.no-sidebar {

    .entry-meta {
        margin-top: 1.5em;
    }

    @media screen and (min-width: $query__med) {
        .post-content__wrap {

            .entry-meta {
                margin-top: 2.25em;
                font-size: 80%;

                a {
                    font-size: 110%;
                    display: block;
                    border-bottom: 3px solid white;

                    &:hover,
                        &:focus {
                        border-bottom-color: $color__interactive;
                    }
                }
            }

            .byline,
            .posted-on,
            .comments-link,
            .edit-link {
                display: block;
                margin-bottom: 1em;

                &:before,
                    &:after {
                    margin: 0;
                    content: "";
                }
            }
            .byline{
                font-style: italic;
                font-size:.6em;
                a{
                    font-style:normal;
                    font-size:1rem;
                }
            }
            .posted-on{
                font-style: italic;
                font-size:.6em;
                a{
                    font-style:normal;
                    font-size:1rem;
                }
            }

            .extra {
                display: inline;
            }
        }

    }
}

/* Single Post Featured Images */

.full-bleed {
    width: 100vw;
    max-height: 1200px;
    margin-top: 1em;
    margin-left: -1em;
    border-top: 1px solid hsl(0, 0%, 90%);
    border-bottom: 1px solid hsl(0, 0%, 90%);
    overflow: hidden;
    background-color:$color__g2;

    @media screen and (min-width: $query__small) {
        margin-left: -2em;
    }

    @media screen and (min-width: $query__med) {
        margin-top: 1.5em;
        margin-right: -2em;
    }

    @media screen and (min-width: $size__max-width) {
        margin-right: calc( ((-100vw + #{$size__max-width})/2) - 2em );
        margin-left: calc( ((-100vw + #{$size__max-width})/2) - 2em );
    }
    img {
        display: block;
        margin: 0 auto;
    }
}


.page-content,
.entry-content,
.entry-summary {
    margin: 1.5em 0 0;
}

.page-links {
    clear: both;
    margin: 0 0 1.5em;
}

.entry-footer {
    margin: 1.5em 0;
    font-family: $font__aa;
}
